//  store
import axios from 'axios'
import router from '../../router';
import Vue from 'vue';

const memberStore = {
    namespaced: true,
    state: {
        userData: [], //   
        scrapStatus: null,
        loginStatus: false,//  
        loginError: false,
    },
    mutations: {
        // 
        loginSuccess(state, payload) {
            state.loginStatus = true;
            state.loginError = false;
            state.userData = payload;


        },
        // 
        loginFalse(state) {

            state.loginStatus = false;
            state.loginError = true;
        }
    },
    actions: {
        login({ commit, dispatch }, loginData) {
            localStorage.clear();
            sessionStorage.clear();

            axios
                .post('http://localhost:8082/member/login', loginData, {
                    headers: {
                        'Access-Control-Allow-Origin': '*',
                    }
                })
                .then(res => {
                    let token = res.data.token;


                    if (token === undefined) {//    
                        Vue.swal({ text: ",   " })

                        commit('loginFalse')


                    } else {// 

                        if (loginData.rememberMe == false) {//rememberMe false
                            //1.    
                            sessionStorage.setItem("memberEmail", loginData.memberEmail)
                            sessionStorage.setItem("access_token", token);

                        } else {
                            //1.   
                            localStorage.setItem("memberEmail", loginData.memberEmail)
                            localStorage.setItem("access_token", token)//  
                        }
                        dispatch("getMemberInfo", loginData)//  commit('loginSuccess')
                        router.push('/');// 
                    }

                })
                .catch((error) => console.log(error));



        },
        logout({ commit }) {
            //localStroage  
            window.localStorage.clear();
            window.sessionStorage.clear();
            window.sessionStorage.removeItem('vuex');

            // location.reload();
            commit('loginFalse');
            router.go('/');
        },
        deleteMember({ commit }, loginData) {
            axios.post("http://localhost:8082/member/deleteMember", loginData, {
                headers: {
                    'Access-Control-Allow-Origin': '*',
                }
            }) //form server 
                .then((res) => {
                    if (res.data > 0) {
                        // ok
                        Vue.swal({
                            text: " .  ItJobGo  .",
                            icon: "success"
                        })
                        sessionStorage.clear();
                        localStorage.clear();
                        commit('loginFalse');
                        router.push('/');//  
                    } else if (res.data == -1) {//  

                        Vue.swal({
                            text: " .  ",
                            icon: "error"
                        });
                    } else {
                        Vue.swal({
                            text: " .      ",
                            icon: "error"
                        });
                    }
                })
                .catch(() => {
                    Vue.swal({
                        text: " .  ",
                        icon: "error"
                    });
                });
        },
        //  
        async getMemberInfo({ commit }) {
            var memberEmail = null;
            var token = null;

            if (localStorage.getItem("memberEmail")) {

                memberEmail = localStorage.getItem("memberEmail")
                token = localStorage.getItem("access_token")

            } else {

                memberEmail = sessionStorage.getItem("memberEmail")
                token = sessionStorage.getItem("access_token")
            }


            let config = {
                //   
                headers: {
                    "access-token": token,

                    'Access-Control-Allow-Origin': '*',

                }
            }
            if (token != null || memberEmail != null) {
                // member return  
                await axios.get('http://localhost:8082/member/getMember?memberEmail=' + memberEmail, config)
                    .then(response => {

                        var userData = {
                            memberSq: response.data.memberSq,
                            memberAddr: response.data.memberAddr,
                            memberAddrDtl: response.data.memberAddrDtl,
                            memberAddrExtra: response.data.memberAddrExtra,
                            memberEmail: response.data.memberEmail,
                            memberLevel: response.data.memberLevel,
                            memberName: response.data.memberName,
                            memberPhone: response.data.memberPhone,
                            memberPostCode: response.data.memberPostCode,
                            memberPosition: response.data.memberPosition,
                            memberToken: response.data.memberToken
                        }

                        commit('loginSuccess', userData)
                    })
                    .catch(() => {

                        commit('loginFalse');
                    })
            } else {

                commit('loginFalse');
            }
        },


    }

}
export default memberStore