<template>
  <div class="container-fluid">
    <div class="row">
      <div class="submenuimage">
        <p class="subtitle">WAITING LIST</p>
      </div>
      <div class="container">
        <v-tabs centered color="grey darken-3">
          <v-tab to="/meetingapply"><b></b></v-tab>
          <v-tab
            :to="{ name: 'mkmeeting', params: { memberSq: userData.memberSq } }"
            ><b></b></v-tab
          >
          <v-tab
            :to="{ name: 'approve', params: { memberSq: userData.memberSq } }"
            ><b></b></v-tab
          >
          <v-tab
            :to="{ name: 'waitList', params: { memberSq: userData.memberSq } }"
            ><b>  </b></v-tab
          >

          <v-tabs-slider color="deep-purple lighten-5"></v-tabs-slider>
        </v-tabs>
      </div>
    </div>
    <b-container>
      <p class="text-center">
        {{ userData.memberName }}    .  
        
      </p>
      <b-row>
        <b-col>
          <v-data-table
            :headers="headers"
            class="row-pointer mt-4"
            :items="apply"
            item-key="name"
          >
            <template v-slot:item.status="{ item }">
              <b-button @click="approve(item, index)"></b-button>
              <b-button @click="unapproved(item, index)"></b-button>
            </template>
          </v-data-table>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapState } from "vuex";
const { mapState: loadUserState } = createNamespacedHelpers("memberStore");

export default {
  data() {
    return {
      memberSq: "",
      headers: [
        {
          text: "",
          align: "start",
          filterable: false,
          value: "no",
        },
        //  spring  json    row value  
        { text: "", value: "position" },
        { text: "", value: "username" },
        { text: "", value: "collname" },
        { text: "/", value: "status" },
      ],
    };
  },
  created() {
    //     
    let email = sessionStorage.getItem("memberEmail");
    this.$store.dispatch("FECH_MEETINGAPPLY", email);
  },
  computed: {
    ...mapState({
      apply: (state) => state.apply,
    }),
    ...loadUserState(["userData"]),
  },
  methods: {
    approve(no, index) {
      let check = confirm("?");
      if (check == true) {
        this.$store.dispatch("FECH_APPROVE", no.no).then(() => {
          this.apply.splice(index, 1);
        });
      } else return;
    },
    unapproved(no, index) {
      let check = confirm("?");
      if (check == true) {
        this.$store.dispatch("FECH_UNAPPROVE", no.no).then(() => {
          this.apply.splice(index, 1);
        });
      } else return;
    },
  },
  unValue() {
    alert(" ");
  },
  test() {
    alert(" ");
  },
};
</script>

<style scoped>
.submenuimage {
  background-image: url("../../assets/images/meeting-status.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  opacity: 0.7;
  height: 180px;
  background-color: #f4eeff;
  text-align: center;
  line-height: 180px;
}
.submenuimage {
  width: 100%;
  height: 180px;
  background-color: #f4eeff;
  text-align: center;
  line-height: 180px;
}

.subtitle {
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 700;
  color: #fff;
  text-shadow: 2px 2px #4e515763;
  font-size: 50px;
}

/* header css */
.row-pointer >>> thead tr {
  background-color: #ededed;
  border-top: 2px solid #d5d5d5;
}
.row-pointer >>> thead tr th span {
  font-size: 15px;
  font-weight: bold;
  color: #4e5157;
}
/* hover */
.row-pointer >>> tbody tr :hover {
  cursor: pointer;
}
</style>
