              >
              <b-dropdown-item to="/enrollmeeting"></b-dropdown-item>
              <b-dropdown-item to="/portfolioList"></b-dropdown-item>
            </b-nav-item-dropdown>

            <b-nav-item-dropdown class="nav-link" text="STUDY">
