              <b-dropdown-item to="/refSite"> </b-dropdown-item>
            </b-nav-item-dropdown>

            <!-- Commnunity -->
            <b-nav-item-dropdown class="nav-link" text="COMMUNITY">
              <b-dropdown-item to="/noticeList"></b-dropdown-item>
              <b-dropdown-item to="/itNewsList">IT</b-dropdown-item>
              <b-dropdown-item to="/communityBoardList"
                >
              </b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>

          <!-- Right aligned nav items -->
          <b-navbar-nav class="ml-auto">
