<template>
  <div class="container-fluid">
    <div class="row">
      <div class="submenuimage">
        <p class="subtitle">CREATED MEETING LIST</p>
      </div>
      <div class="container">
        <v-tabs centered color="grey darken-3">
          <v-tab to="/meetingapply"><b></b></v-tab>
          <v-tab
            :to="{ name: 'mkmeeting', params: { memberSq: userData.memberSq } }"
            ><b></b></v-tab
          >
          <v-tab
            :to="{ name: 'approve', params: { memberSq: userData.memberSq } }"
            ><b></b></v-tab
          >
          <v-tab
            :to="{
              name: 'waitList',
              params: { memberSq: userData.memberSq },
            }"
            ><b>  </b></v-tab
          >
          <v-tabs-slider color="deep-purple lighten-5"></v-tabs-slider>
        </v-tabs>
      </div>
    </div>
    <b-container>
      <p class="text-center">
        {{ userData.memberName }}   
      </p>
      <b-row>
        <b-col>
          <v-data-table
            class="row-pointer mt-4"
            :headers="headers"
            :items="mklist"
            item-key="name"
          >
            <template v-slot:item="props">
              <tr>
                <td class="text-xs-right">{{ props.item.collabSq }}</td>
                <td
                  class="text-xs-right"
                  @click="movemeeting(props.item.collabSq)"
                >
                  {{ props.item.title }}
                </td>
                <td class="text-xs-right">
                  {{ new Date(props.item.mdate).toLocaleDateString() }}
                </td>
                <td class="text-xs-right">
                  <b-button @click="update(props.item)"></b-button
                  ><b-button @click="deltemet(props.item, props.index)"
                    ></b-button
                  >
                </td>
                <td class="text-xs-right">
                  <b-button
                    v-b-toggle.sidebar-backdrop
                    @click="applylist(props.item.collabSq)"
                    ></b-button
                  >
                </td>
              </tr>
            </template>
          </v-data-table>
          <b-sidebar
            id="sidebar-backdrop"
            title="  "
            :backdrop-variant="variant"
            backdrop
            shadow
          >
            <div class="px-3 py-2">
              <b-card title="">
                <div v-for="user in enter" :key="user.id">
                  <b-avatar size="sm"></b-avatar>
                  <p class="enteruser">
                    {{
                      `${user.MEMBER_NAME}/${user.MEMBER_EMAIL}/${user.POSITION}`
                    }}
                  </p>
                </div>
              </b-card>
            </div>
          </b-sidebar>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapState } from "vuex";
const { mapState: loadUserState } = createNamespacedHelpers("memberStore");

export default {
  data() {
    return {
      memberSq: "",
      enter: [],
      variant: "dark",
      headers: [
        {
          text: "",
          align: "start",
          filterable: false,
          value: "collabSq",
        },
        //  spring  json    row value  
        { text: "", value: "title" },
        { text: "", value: "mdate" },
        { text: "/", value: "status" },
        { text: "" },
      ],
    };
  },
  created() {
    //     
    const no = this.$route.params.memberSq;
    this.$store.dispatch("FECH_MKLIST", no);
  },
  computed: {
    ...mapState({
      mklist: (state) => state.mklist,
    }),
    ...loadUserState(["userData"]),
  },
  methods: {
    applylist(no) {
      this.$store.dispatch("FECH_APPLYLIST", no).then(({ data }) => {
        this.enter = data;
      });
    },
    movemeeting(item) {
      this.$router.push({ name: "meetinginfo", params: { id: item } });
    },
    deltemet(item, index) {
      let check = confirm("?");
      if (check == true) {
        const no = item.collabSq;
        this.$store.dispatch("FECH_MEETINGDEL", no).then(() => {
          this.mklist.splice(index, 1);
        });
      } else return;
    },
    update(item) {
      let update = confirm("?");
      if (update == true) {
        const uno = item.collabSq;
        this.$router.push({ name: "meetingupdate", params: { id: uno } });
      }
    },
  },
};
</script>

<style scoped>
.submenuimage {
  background-image: url("../../assets/images/meeting-status.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  opacity: 0.7;
  height: 180px;
  background-color: #f4eeff;
  text-align: center;
  line-height: 180px;
}
.submenuimage {
  width: 100%;
  height: 180px;
  background-color: #f4eeff;
  text-align: center;
  line-height: 180px;
}

.subtitle {
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 700;
  color: #fff;
  text-shadow: 2px 2px #4e515763;
  font-size: 50px;
}
#approve {
  color: blue;
}
#unpprove {
  color: red;
}
.enteruser {
  font-size: 14px;
}
* {
  font-family: "Noto Sans KR", sans-serif;
}

/* header css */
.row-pointer >>> thead tr {
  background-color: #ededed;
  border-top: 2px solid #d5d5d5;
}
.row-pointer >>> thead tr th span {
  font-size: 15px;
  font-weight: bold;
  color: #4e5157;
}
/* hover */
.row-pointer >>> tbody tr :hover {
  cursor: pointer;
}
</style>
