import $ from "jquery";

// 
$(document).on("click", ".areaBtn", function() {
  let tem = $(this) //   value return
    .parents(".area-btn")
    .attr("value");
  if (
    //
    $(this)
      .parents(".area-btn") //      on 
      .hasClass("on")
  ) {
    $(this)
      .parents(".area-btn") //on 
      .removeClass("on");
    $(".city-layer ." + tem).removeClass("on"); // dept2 on
    // dept2
  } else {
    $(this)
      .parents(".area-btn") // on 
      .addClass("on");
    $("." + tem).addClass("on"); // dept2 on

    $(".city-layer")
      .not(document.getElementsByClassName(tem))
      .removeClass("on"); // layer  on remove
    $(".areaBtn")
      .not(this)
      .parents(".area-btn") //  on
      .removeClass("on");
  }
});

//  
$(document).on("click", ".jobBtn", function() {
  let tem = $(this) //   value return
    .parents(".job-btn")
    .attr("value");
