    $(this)
      .parents(".job-btn") // on 
      .addClass("on");
    $("." + tem).addClass("on"); // dept2 on

    $(".job-detail-layer")
      .not(document.getElementsByClassName(tem))
      .removeClass("on"); // layer  on remove
    $(".jobBtn")
      .not(this)
      .parents(".job-btn") //  on
      .removeClass("on");
  }
});

